.search {
  min-height: 81vh;
}

.searchbar {
  z-index: 1;
  color: #fff;
  background-color: #333;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding: 12px 24px;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 2px 4px -1px #0003, 0 4px 5px #00000024, 0 1px 10px #0000001f;
}

.searchForm {
  background-color: #fff;
  border-radius: 3px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  display: flex;
  overflow: hidden;
}

.searchBtn {
  opacity: .6;
  cursor: pointer;
  background-image: url("search-icon.6319a08a.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40%;
  border: 0;
  outline: none;
  width: 48px;
  height: 48px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.searchBtn:hover {
  opacity: 1;
}

.searchBtn-label {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.searchInput {
  font: inherit;
  border: none;
  outline: none;
  width: 100%;
  padding-left: 4px;
  padding-right: 4px;
  font-size: 20px;
  display: inline-block;
}

.searchInput::placeholder {
  font: inherit;
  font-size: 18px;
}

.strain-item {
  cursor: pointer;
  border: 1px solid #ddd;
  margin: 10px;
  padding: 15px;
  transition: transform .3s, background-color .3s, color .3s;
}

.strain-item:hover {
  color: #007bff;
  background-color: #f5f5f5;
  transform: scale(1.05);
}

.strain-info {
  color: #333;
  font-size: 18px;
  font-weight: bold;
}

.icons {
  align-items: center;
  margin-left: 10px;
  display: flex;
}

.icon {
  width: 40px;
  height: 40px;
  margin-right: 5px;
}

.hidden-checkbox {
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
  position: absolute;
}

#bacteriaCheckbox:not(:checked) + label #bacteriaIcon {
  content: url("bacteria-disabled.1ed1d03e.png");
}

#bacteriaCheckbox:checked + label #bacteriaIcon {
  content: url("bacteria-active.c945ce47.png");
}

#moldCheckbox:not(:checked) + label #moldIcon {
  content: url("mold-disabled.cc513afa.png");
}

#moldCheckbox:checked + label #moldIcon {
  content: url("mold-active.84897d3c.png");
}

#yeastCheckbox:not(:checked) + label #yeastIcon {
  content: url("yeast-disabled.c2cb1d37.png");
}

#yeastCheckbox:checked + label #yeastIcon {
  content: url("yeast-active.6514cee0.png");
}

#virusCheckbox:not(:checked) + label #virusIcon {
  content: url("virus-disabled.daadf2a3.png");
}

#virusCheckbox:checked + label #virusIcon {
  content: url("virus-active.76573cb1.png");
}

@media (max-width: 480px) {
  .searchbar {
    flex-direction: column;
    align-items: center;
    padding: 12px;
  }

  .icons {
    margin-top: 10px;
  }
}

.modal {
  z-index: 2;
  background-color: #000000b3;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #f2f2f2;
  border: 1px solid #888;
  border-radius: 10px;
  width: 70%;
  margin: 15% auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.modal-title {
  color: #333;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
}

.modal-info-list {
  padding: 0;
  list-style-type: none;
}

.section-header {
  color: #007bff;
  margin-top: 15px;
  margin-bottom: 5px;
  font-weight: bold;
}

.modal-info-item {
  color: #555;
  margin-bottom: 8px;
}

.modal-link {
  color: #007bff;
  text-decoration: none;
  transition: color .3s;
}

.modal-link:hover {
  color: #0056b3;
  text-decoration: underline;
}

.pagination-container {
  justify-content: center;
  align-items: center;
  width: calc(100% - 2rem);
  padding: 1rem 0;
  display: flex;
  bottom: 0;
}

.pagination-number, .pagination-button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: .2rem;
  width: 2.5rem;
  height: 2.5rem;
  margin: .25rem;
  font-size: 1.1rem;
}

.disabled {
  color: #fff;
}

.active {
  color: #fff;
  background: #0085b6;
}

.hidden {
  display: none;
}

